body{
    text-align: right;
    &.over{
        overflow: hidden;
    }
}

.topbar{
    padding: 18px 0;
    background-color: #000;
    overflow: hidden;
    @media (max-width:768px) {
        display: none;
    }
    .content{
        display: flex;
        justify-content: space-between;
        .left{
            display: flex;

            a{
                font-size: 13px;
                font-family: "openSans";
                color: #fff;
                text-decoration: none;
                margin-left: 30px;
                display: flex;
                align-items: center;
                img{
                    margin-left: 10px;
                }
            }
        }
        .right{
            a {
                text-decoration: none;
                outline: none;
                font-size: 18px;
                color: rgb(247, 247, 247);
                margin-right: 28px;
                transition: .3s;
                text-shadow:  0 20px transparent;
                &:hover{
                    text-shadow:  0 0 #e11d27; 
                    color: transparent;
                }
        }
        }
    }

}
html[dir="ltr"]{
    body{text-align: left;}
    .topbar .content .left a {
        margin-right: 30px;
        margin-left: 0;
    }
    .topbar .content .left a img{
        margin-left: 0;
        margin-right: 10px;
    }
    .topbar .content .right a img{
        margin-left:28px;
        margin-right: 0;
    }
    
}
.main-nav{
    background-color: #fff;
    padding: 30px 0;
    display: flex;
    align-items: center;
    @media (max-width:991px){
        padding: 10px 0;
    }
    .logo{
        text-decoration: none;
        display: flex;
        align-items: center;
        @media (max-width:375px) {
          img{
                width: 50px;
            }
        }

        h1{
            font-family: "BahijBold75";
            color:#000;
            font-size: 20px;
            margin-bottom: 0;
            
            margin-right: 10px;
            html[dir="ltr"] & {
                margin-right:0;
                margin-left:10px;
            }
            
            span{
                font-family: "Bahij45Light";
                color:#868484;
                display: block;
                font-size: 14px;

            }
            @media (max-width:375px) {
                font-size: 13px;
                span{
                    font-size:7px;
    
                }
                margin-right: 5px;

            }
      
        }

    }
    .linkCon{
        display: flex;
        ul{
            list-style: none;
            display: flex;
            margin-bottom: 0;
            align-items: center;
        }
    }
    .content{
        display: flex;
        justify-content: space-between;
        .links-list{
            @media (max-width:991px){
                display: none;
            }
            @media (max-width:1024px) {
                html[dir="ltr"] &{
                    padding-inline-start: 0;

                }
    
            }

            li{
                margin-right: 27px;
    
                a{
                    color: #000;
                    font-size: 15px;
                    font-family: "BahijBold75";
                    text-decoration: none;
                    transition: .3s;
                    text-shadow:  0 20px transparent;
                    i{
                        margin-left: 10px;
                    }
                    &:hover,
                    &.ative{
                        text-shadow:  0 0 #e11d27; 
                        color: transparent;
                    }
                }
            }
        }
        .searchBtn{
            background-color: transparent;
            border: none;
            outline: none;
            cursor: pointer;
            transition: .3s;
            &:hover{
                color: #e11d27;
            }
            @media (max-width:991px){
                font-size: 20px;
            }    

        }
        .list2{
            padding-inline-start: 30px;
            @media (max-width:375px) {
                padding-inline-start: 0;
            }

        
        }
        .bars{
            display: none;
            @media (max-width:991px) {
                display: block;
            }
            button{

                font-size: 23px;
                background-color: transparent;
                border: none;
                outline: none;
                cursor: pointer;
                transition: .3s;
                &:hover{
                    color: #e11d27;
                }
            }

        }
        .lang{
            margin-right: 30px;
            @media (max-width:991px) {
                margin-right: 10px;
            }

            span{
                @media (max-width:991px){
                    display: none;
                }    
            }
            a{
                font-family: "ElMessiri";
                color: #000;
                font-size: 13px;
                text-decoration: none;
                transition: .3s;
                &:hover{
                    color: #e11d27;
                }
    
                img{
                    margin-left: 13px;
                    @media (max-width:991px) {
                        margin-left: 0;
                    }
        
                }
            }
        }
    }
}
.nav-popUp{
    position: fixed;
    background-color: rgba(0, 0, 0,0.831);
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: .5s;
    .close-nav{
        border: none;
        cursor: pointer;
        background-color: transparent;
        font-size: 30px;
        color: #e11d27;
        position: absolute;
        top: 17%;
        left: 17%;
        html[dir="ltr"] & {
            left:unset;
            right:0;
        }
        transform: translate(-50%,-50%);
        transform: translateY(100%);
        opacity: 0;
        transition: .5s;
        transition-delay: .820ms;

        outline: none;
        &:hover{
            color: rgba(#e11d27, .5);

        }
    }

    .links-list{
        display: flex;
        flex-direction: column;
        list-style: none;
        margin: 0;
        transition: .5s;
        width: max-content;
        li{
            opacity: 0;
            &:not(:last-of-type){
                margin-bottom: 20px;
            }
            transition: .5s;
            &:nth-of-type(1){
                transform: translateY(30px);
                transition-delay: .320ms;
            }
            &:nth-of-type(2){
                transform: translateY(60px);
                transition-delay: .420ms;

            }
            &:nth-of-type(3){
                transform: translateY(90px);
                transition-delay: .520ms;

            }
            &:nth-of-type(4){
                transform: translateY(120px);
                transition-delay: .6200ms;

            }
            &:nth-of-type(5){
                transform: translateY(150px);
                transition-delay: .720ms;

            }
            a{
                color: #fff;
                text-decoration: none;
                font-size: 20px;
                font-family: "BahijBold75";
                i{
                    margin-left: 20px;
                }
            }
        }
    }
    &.active{
        opacity: 1;
        visibility: visible;
        .close-nav{
            transform: translate(-50%,-50%);
            opacity: 1;
        }

        .links-list{
            li{
                opacity: 1;

                &:nth-of-type(1){
                    transform: translateY(0);
                }
                &:nth-of-type(2){
                    transform: translateY(0);
    
                }
                &:nth-of-type(3){
                    transform: translateY(0);
    
                }
                &:nth-of-type(4){
                    transform: translateY(0);
    
                }
                &:nth-of-type(5){
                    transform: translateY(0);
    
                }
            }
        }
    
    }
}
.search-popUp{
    position: fixed;
    background-color: rgba(0, 0, 0,0.831);
    top:0;
    left:0;
    right: 0;
    bottom: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: .5s;
    .close-search{
        border: none;
        cursor: pointer;
        background-color: transparent;
        font-size: 30px;
        color: #e11d27;
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translate(-50%,200%);
        // transform: translateY(200%);
        opacity: 0;
        transition: .5s;
        transition-delay: .400ms;

        outline: none;
        &:hover{
            color: rgba(#e11d27, .5);

        }

    }
    .input-container{
        margin: auto;
        width: max-content;
        position: relative;
        transform: translateY(200%);
        opacity: 0;
        transition: .5s;
        transition-delay: .2s;

        input{
            border: none;
            outline: none;
            width: 500px;
            height: 40px    ;
            @media (max-width:480px) {
                width: 300px;
            }
            padding-right:26px;
            font-family: "Bahij45Light";
            outline: none;
            transition: .5s;
            border:1px solid transparent;

            &:focus{
                border:1px solid #e11d27;
                
            }
            &::placeholder{
                font-size: 17px;
                color: #868484;
            }
        }
        
        button{
            position: absolute;
            outline: none;
            top:0;
            left: 0;
            height: 100%;
            cursor: pointer;
            border: none;
            width: 84px;
            background-color: rgb(33, 33, 33);
            font-size: 17px;
            color: rgb(248, 248, 248);
            font-family: "Bahij45Light";
            transition: .5s;
            i{
                transition: .5s;
            }
            &:hover{
                background-color: #e11d27;
                i{
                    transform: translateY(-2px);
                }
            }
        }
    }
    &.active{
        opacity: 1;
        visibility: visible;
        .close-search{
            transform: translate(-50%,-50%);
            opacity: 1;
        }
        .input-container{
            opacity: 1;
            transform: translateY(0);

        }
    }
}
html[dir="ltr"]{
    .nav-popUp .links-list li a i {
        margin-left: 0px;
        margin-right: 20px;
    }
    
    .main-nav{
        .content{
