}
.header{
    background-color: #000;
    min-height: 200px;
    .swiper-slide{
        position: relative;
        img{
            width: 100%;
            min-height: 300px;
        }
        .content{
            color: #fff;
            position: absolute;
            top: 50%;
            left: 60%;
            transform: translate(-50%,-50%);
            width: 50%; 
            @media (max-width:480px) {
                width: 100%; 
                left: 50%;
            }
            @media (max-width:480px) {
                text-align: center;
            }
            @media (max-width:425px) {
                top: 45%;
            }

            .topTitle{
                font-size: 20px;
                font-family: "Bahij45Light";
                margin-bottom: 15px;
                @media (max-width:480px) {
                    font-size: 17px;
                }
                @media (max-width:320px) {
                    font-size: 16px;
                }

            }
            .title{
                font-size: 48px;
                font-family: "BahijBold75";
                margin-bottom: 50px;
                @media (max-width:1024px) {
                    font-size: 30px;
                }
                @media (max-width:768px) {
                    font-size: 27px;
                    margin-bottom: 30px;
                }
                @media (max-width:480px) {
                    font-size: 23px;
                    margin-bottom: 25px;

                }
                @media (max-width:375px) {
                    font-size: 19px;
                }
    
                span{
                    display: block;
                    @media (max-width:768px) {
                        display: inline-block;

                    }
    
                }                  
            }
            .btn-container{
                display: flex;
                @media (max-width:480px) {
                    justify-content: center;
                    align-items: center;
                }

                a{
                    width: 134px;
                    height: 40px;
                    font-size: 16px;

                    @media (max-width:480px) {
                        width: 100px;
                        height: 30px;
                        font-size: 13px;

                    }
                    @media (max-width:375px) {
                        width: 100px;
                        height: 27px;
                        font-size: 12px;
                    }
    
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: #fff;
                    text-decoration: none;
                    font-family: "Bahij55Roman";
                    position: relative;
                    transition: .5s;
                    &::before{
                        content: '';
                        border: 1px solid #fff;
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        transition: .5s;
                        bottom: 0;
                        left: 0;

                    }
                    &:not(:last-child){
                        margin-left: 37px;
                        @media (max-width:375px) {
                            margin-left: 20px;
                        }
                
                    }
                    &:hover{
                        background-color: #e11d27;
                        &::before{
                            width: 10px;
                            height: 10px;
                            border: 5px solid #fff;
                        }
    
                    }
                }
            }
        }
    }
    .swiper-container-horizontal>.swiper-pagination-bullets{
        bottom: 25px;
        @media (max-width:480px) {
            bottom: 5px;
        }
    }
    .swiper-pagination-bullet{
        width: 10px;
        height: 10px;
        opacity: 1;
        border: 2px solid #fff;
        background-color: unset;
        transition: .3s;
    
    }
    .swiper-pagination-bullet-active{
        background-color: unset;
        width: 10px;
        height: 10px;
        border: 2px solid #e11d27;

    }
}
html[dir="ltr"]{
