            width:116px ;
            display: flex;
            justify-content: center;
            align-items: center;
            &:first-of-type{
                margin-left: 40px;
                @media (max-width:991px) {
                    margin-left: 20px;
                
                }
            }
            transition: .5s;
            position: relative;
            &::before{
                content: '';
                position: absolute;
                bottom:-13px;
                left:50%;
                transform: translateX(-50%);
                border: 8px solid transparent;
                border-left-color: #e11d27;
                border-top-color:  #e11d27;
                opacity: 0;
                transition:.4s;
            }
            &.active{
                background-color: #e11d27;
                color: #fff;
                &::before{
                    opacity: 1;
                }
            }
        }
    }
    .content-box{
        margin-top: 50px;
        @media (max-width:991px) {
            margin-top: 30px;
        
        }
    
    }
    .content{
        width: max-content;
        overflow: hidden;
        position: relative;
        @media (max-width:991px) {
            width: calc(100%/2.5);
            
        }
        @media (max-width:375px) {
            width: calc(100%/1.5);

        }


        .overlay{
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            display: flex;
            flex-direction: column;
            padding: 54px 22px;
            color: rgb(255, 255, 255);
            transition: .5s;
            opacity: 0;
            @media (max-width:480px) {
                padding: 20px;

            }
            @media (max-width:375px) {
                padding: 30px 20px;
                
            }
            h3{
                margin-top: auto;
                font-size: 19px;
                font-family: "BahijBold75";
                transform: translateY(100px);
                transition: .3s;
                transition-delay: .5s;
                opacity: 0;
            }
            p{
                font-size: 16px;
                font-family: "Bahij45Light";
                transform: translateY(100px);
                transition: .5s;
                transition-delay: .5s;
                opacity: 0;
                @media (max-width:480px) {
                    font-size: 13px;
    
                }

            }
            .red-pals{
                position: absolute;
                bottom: 0;
                left: 0;
                background-color: #e11d27;
                width:20px;
                height: 17px;
                transition: .5s;
                font-size: 28px;
                font-family: "ElMessiri";
                display: flex;
                justify-content: center;
                align-items: center;
                html[dir='ltr'] & {
                    left: unset;
                    right:0;
                }
            }
        }
        &::before{
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 10%;
            height: 10%;
            background-color: rgba(0,0,0,.4) ;
            border: 8px solid #e11d27;
            transition: .5s;
            opacity: 0;
            html[dir='ltr'] & {
                left: unset;
                right:0;
            }
        }
        &:hover{
            .overlay{
                opacity: 1;
                h3{
                    transform: translateY(0);
                    opacity: 1;

                }
                p{
                    transform: translateY(0);   
                    opacity: 1;
                }
    
                .red-pals{
                    width:55px;
                    height: 47px;
                    @media (max-width:480px) {
                        width:40px;
                        height: 37px;
        
                    }
    
                }
            }
            &::before{
                width: 100%;
                height: 100%;
                opacity: 1;
            } 
        }
    }
    .addBtn{
        margin-right: auto;
        margin-left: auto;
        color:#000;
        margin-top: 40px;
        &::before{
            border: 1px solid #000;    
        }
        &:hover{
            color: #fff;
            text-decoration: none;
            &::before{
                border: 1px solid #e11d27;
                border-top-width: 20px;
                border-bottom-width: 20px;
                border-left-width: 60px;
                border-right-width: 60px;
            }
            @media (max-width: 375px){
                &::before {
                    border-top-width: 10px;
                    border-bottom-width: 10px;
                    border-left-width: 50px;
                    border-right-width: 50px;
                }

            }
            
        }
    
    }
}
html[dir="ltr"]{
    .projects-section .projects-nav{
        margin-right: 0;
