.customers-section{
    padding: 60px 0 70px;
    .container{
        position: relative;
    }
    .swiper-slide{
        border: 1px solid rgba(2, 2, 2 ,  0.051);          
        display: flex;
        justify-content: center;
        align-items: center;
        height: 164px;
        width: 222px;
        transition: .5s;
        position: relative;
        img{
            opacity:  0.169;
            transition: .5s;
        }
        .myborder{
            position: absolute;
            width: 18%;
            height: 18%;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            border: 5px solid #e11d27;
            transition: .5s;
            opacity: 0;
            &::before{
                content: '';
                position: absolute;
                bottom: 0;
                right: 0;
                border: 13px solid transparent;
                border-right-color: #e11d27;
                border-bottom-color: #e11d27;
 
            }
            &::after{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                border: 10px solid transparent;
                border-left-color: #e11d27;
                border-top-color: #e11d27;
 
            }
        }
        &:hover{
            img{
                opacity:  1;
            }
            .myborder{
                width: 100%;
                height: 100%;
                opacity: 1;
            }    
        }
    }
    .swiper-pagination {
        bottom: -40px;
        display: none;
        @media (max-width:991px) {
            display: block;
            
        }
        left: 50%;
        transform: translateX(-50%);
    }
    
    
    .swiper-pagination-bullet{
        width: 10px;
        height: 10px;
        opacity: 1;
        border: 2px solid #000;
        background-color: unset;
        transition: .3s;
        margin: 5px;
    
    }
    .swiper-pagination-bullet-active{
        background-color: unset;
        width: 10px;
        height: 10px;
        border: 2px solid #e11d27;

    }

    .arr{
        top: 65%;
        &.my-button-prev {
            right: -40px;
        }
        &.my-button-next {
            left: -40px;
        }
        @media (max-width:1024px) {
            &.my-button-prev {
                right: -20px;
            }
            &.my-button-next {
                left: -20px;
            }
    
        }
        @media (max-width:991px) {
            &.my-button-prev {
                right: 30px;
            }
            &.my-button-next {
                left: 30px;
            }
    
        }

        
    }
    
}

