@font-face {
    font-family: "openSans";
    src: url("../fonts/OpenSans-Light.ttf");
}
@font-face {
    font-family: "ElMessiri";
    src: url("../fonts/ElMessiri-Regular-1.ttf");
}
@font-face {
    font-family: "Bahij45Light";
    src: url("../fonts/Bahij45Light.ttf");
}
@font-face {
    font-family: "Bahij55Roman";
    src: url("../fonts/Bahij55Roman.ttf");
}
@font-face {
    font-family: "BahijBold75";
    src: url("../fonts/BahijBold75.ttf");
}
html{
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #e11d27;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #000;
    }
}
  
.nav-link {
    padding:0;
}

.arr{
    position: absolute;
    color: #fff;
    background-color: rgb(77, 77, 77);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    top: 50%;
    z-index: 20;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: .5s;
    cursor: pointer;
    outline: none;
    @media (max-width:991px) {
        display: none;
    }
    img{
        position: absolute;
    }
    &.my-button-next{
        left: 40px;
        img{
            right: 0;
            transition: .5s;

        }
    }
    &.my-button-prev{
        right: 40px;
        img{
            left: 0;
            transition: .5s;

        }
    }
    &:hover{
        background-color: #e11d27;
        &.my-button-next{
            img{
                right: 35%;
            }
        }
        &.my-button-prev{
            img{
                left: 35%;
            }
        }

    }
}


.main-title{
    img{
        position: absolute;
        right: 0;
        bottom: -5px;
        z-index: 1;
    
    }
    .title{
        position: relative;
        z-index: 2;
        font-family: "BahijBold75";
        font-size: 34px;
        padding-right: 23px;
    }
    position: relative;
    width: max-content;
    &:before{
        position: absolute;
        border-radius: 50%;
        left: -15px;
        top: 65%;
        content: '';
        width: 7px;
        height: 7px;
        background-color: #e11d27;
    }
}
.main-titleCon{
    width: max-content;
    @media (max-width:991px) {
        width: unset;
    }
    p{
        width: 80%;
        font-size: 12px;
        color: rgb(134, 132, 132);
        font-family: "Bahij45Light";
        margin-top: 20px;
        @media (max-width:375px) {
            width: 100%;
        }
    

    }
}
html[dir="ltr"]{
    .main-title{
        margin-right: auto;
        @media (max-width:480px) {
            left: 0px;
           }

        img{
            left: -20px;
            @media (max-width:480px) {
             left: 0px;
            }
            right: unset;
            transform: rotate(180deg);
            @media (max-width:480px) {
                width: 50px;
            }
            @media (max-width:375px) {
                width: 45px;
            }
        }
        &:before{
            left: unset;
            right: -10px;
            @media (max-width:480px) {
                right: -15px;
    
            }
    
        }
    }
 .main-title .title{
        padding-right: 0;
        @media (max-width:480px) {
            font-size: 25px;
            padding-left: 22px;

        }
        @media (max-width:375px) {
            font-size: 20px;
        }
    }
}

.addBtn{
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    position: relative;
    color: #fff;
    width: 130px;
    height: 40px;
    @media (max-width:375px) {
        width: 100px;
        height: 30px;
    }

    font-family: "Bahij45Light";
    &::before{
        content: '';
        border: 1px solid #fff;
        position: absolute;
        width: 100%;
        height: 100%;
        transition: .5s;
        bottom: 0;
        left: 0;
        z-index: -1;

    }
    transition: .5s;
    z-index: 2;
    span{
        margin-right: auto;
        &:last-of-type{
            margin-left: 17px;
            font-family: "ElMessiri";
            margin-top: 4px ;
            font-size: 17px ;
        }   
    }
    &:hover{
        color: #fff;
        text-decoration: none;
        &::before{
            border: 1px solid #e11d27;
            border-top-width: 20px;
            border-bottom-width: 20px;
            border-left-width: 60px;
            border-right-width: 60px;
            @media (max-width:375px) {
                border-top-width: 10px;
                border-bottom-width: 10px;
                border-left-width: 50px;
                border-right-width: 50px;
            }
        
        }

    }
}
html[dir="ltr"]{
    .addBtn{
        span{
            margin-left: auto;
            &:last-of-type{
                margin-right: 16px;
            }
        }
    }
}
.fade.in {
    opacity: 1 !important;
}
.loader{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: radial-gradient(#000000 , #000000);
    z-index: 5000;
    svg{
        position: relative;
        width:150px;
        height:150px;
        animation: roader 2s linear infinite;
        circle{
            width: 100%;
            height: 100%;
            fill: none;
            stroke: #e11d27;
            stroke-linecap: round;
            transform: translate(5px,5px);
            stroke-dasharray: 440;
            stroke-dashoffset: 440;
            animation: roader2 4s linear infinite;

        }
    }
}
@keyframes roader{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    
    }
    
}
@keyframes roader2{
    0%,100%{
        stroke-dashoffset: 440;
    }
    50%{
        stroke-dashoffset: 0;
    }
    50.1%{
        stroke-dashoffset: 880;
    }
}